import React, { Component } from 'react';
import Clock from './components/Clock';
import Day from './components/Day';
import logo from './logo.svg';
import './App.css';

class App extends Component {

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Sam's React Page</h2>
          <p className="App-intro">
            This is where I practice building React components
          </p>
        </div>
        <div>
          <h2>Like a clock!</h2>
          <Clock />
        </div>
        <div>
          <h2>Or a day, with bonus 'today' highlighting circle</h2>
          <Day day={1} month={0} />
          <Day day={4} month={3} />
          <Day day={new Date().getDate()} month={new Date().getMonth()} />
          <Day day={22} month={8} />
          <Day day={25} month={11} />
        </div>
      </div>
    );
  }
}

export default App;



