import React, { Component } from 'react';
import Moment from 'react-moment';
import './Clock.css';

class Clock extends Component{
  constructor(props){
    super(props);
    this.state = {date: new Date()};
  }

  componentDidMount(){
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount(){
    clearInterval(this.timerID);
  }

  tick(){
    this.setState({
      date: new Date()
    });
  }

  render() {
    return (
      <div className="clock-container">
        <div className="clock">
          <Moment format="h:mm">{this.state.date}</Moment>
          <div className="seconds-am-pm">
            <Moment format="ss">{this.state.date}</Moment>
            <Moment format="a">{this.state.date}</Moment>
          </div>
        </div>
      </div>
    );
  }

}

export default Clock;



